/**
 * 
 * 
 * @returns {Function}  
 * 
 * @example 
 * const getRealDomAndOffset = _getRealDomAndOffset() 
 * getRealDomAndOffset(root, cursorOffset, callback, sign) 
 * @param {Element} root  
 * @param {Number} cursorOffset (), getFrontOffset 
 * @param {Function} callback , ,  
 * @param {String} sign getFrontTextcontent, _getRealDomAndOffsetresultok 
 * *** sign_getRealDomAndOffset, getFrontTextcontent[][] 
 */
export const _getRealDomAndOffset = () => {
  let offset = 0
  let ok = false
  let handleGet = (root, cursorOffset, callback, sign) => {
    if (sign !== '_is_not_first_') {
      offset = cursorOffset
      ok = false
    }
    for (let i = 0; i < root.childNodes.length; i += 1) {
      if (ok) return
      const dom = root.childNodes[i]
      if (dom.nodeType === 3) {
        const ctxLength = dom.textContent.length
        const temporary = offset // offset, offsetctxLength0, temporary
        offset -= ctxLength
        if (offset <= 0) { // offset0, dom, temporary
          ok = true
          callback && callback(dom, temporary)
          return
        }
      } else {
        handleGet(dom, offset, callback, '_is_not_first_')
      }
    }
    if (sign !== '_is_not_first_' && !ok) {
      callback && callback(null)
    }
  }
  return handleGet
}

// export const getRealDomAndOffset = _getRealDomAndOffset()

// ---------------------------------------------------------------------------------------------------------------------------------

/**
 * (coreObj) 
 * 
 * @param {Object} target  
 * @param {String} key  
 * 
 * @example 
 * const json = []
 * const key = 'myKey'
 * setCore(json, key)
 * console.log(json)
 */
export const setCore = (target, key) => {
  target[key] = {
    root: null,
    container: null,
    inset: '',
    getFrontOffset: _getFrontOffset(),
    getRealDomAndOffset: _getRealDomAndOffset(),
  }
}

